html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0px;
  padding: 0px;
}

body {
  font-family: "Open Sans", sans-serif, Arial;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
  color: rgb(25, 25, 25);
}

a {
  text-decoration: none;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.container {
  width: 100%;
  margin: 0;
  display: flex;
  height: 100vh;
}

img {
  max-width: 100%;
  display: block;
}

body {
  font-family: "Poppins", sans-serif;
  background-color: #EFF1F6;
  height: 100vh;
  display: flex;
}

/*-----Index-----*/
.container {
  width: 60em;
  height: 600px;
  margin: auto;
  border-radius: 10px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}
@media (max-width: 1000px) {
  .container {
    width: 90%;
  }
}

.info-container {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  padding: 40px 0;
  background-color: white;
  border-radius: 10px 0px 0px 10px;
}
@media (max-width: 1000px) {
  .info-container {
    width: 100%;
    border-radius: 5px;
  }
}
.info-container .title {
  text-transform: capitalize;
  font-size: 3rem;
  font-weight: 600;
  letter-spacing: 1px;
  color: rgb(25, 25, 25);
}

.image-container {
  width: 50%;
  box-sizing: border-box;
  border-radius: 0px 10px 10px 0px;
  overflow: hidden;
}
@media (max-width: 1000px) {
  .image-container {
    display: none;
  }
}
.image-container .imagen_fi {
  width: 120%;
  max-width: 120%;
  height: 100%;
  transform: scale(120%);
}

.btn-container {
  width: 90%;
}

.btn-seleccion {
  width: 80%;
  height: 60px;
  padding: 1.375rem 0.75rem;
  border: 1px solid #007bff;
  border-radius: 0.25rem;
  transition: color 0.15s ease-in-out;
  background-color: white;
  margin: 2.5em 0 0 0;
}

.btn-seleccion:hover {
  background-color: #1EAEFF;
  transition: all ease 0.3s;
}

.jefe-container {
  padding-top: 110px;
}

/* estilo exclusivo del login de alumno */
.alumno-container .title {
  margin-top: 50px;
}

/* IMAGEN */
.biblioteca_central {
  width: 200%;
  max-width: 200%;
  height: 100%;
  transform: translateX(-210px);
}

.form-container {
  margin: 1rem;
  height: 100%;
  width: 85%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.input, .btn {
  width: 90%;
  height: 4.9rem;
  font-size: 1em;
}

.input {
  padding-left: 20px;
  border: none;
  border-radius: 5px 5px 0 0;
  font-weight: 300;
  letter-spacing: 1px;
  box-sizing: border-box;
  background-color: white;
  border-bottom: solid rgb(156, 156, 156) 1px;
  outline: none;
  margin-top: 40px;
}
.input:hover {
  border-bottom: 1px solid black;
}

.btn {
  width: 80%;
  margin-top: 20px;
  font-weight: 600;
  letter-spacing: 1px;
  color: white;
  border: none;
  border-radius: 5px;
  background-color: #1EAEFF;
  cursor: pointer;
}
.btn:hover {
  background-color: #EFF1F6;
  transition: all ease 0.3s;
  color: black;
}

.span {
  background-color: white;
  border-color: transparent;
  padding: 0.5rem;
  margin-bottom: 5px;
  color: black;
  font-weight: 300;
  cursor: pointer;
}
.span:hover {
  color: #1EAEFF;
}